.counter {
    @include circle-style();
    transition: opacity 0.5s ease;
    &--visible {
        opacity: 1;
    }
    &--not-visible {
        opacity: 0;
    }
    span:last-of-type {
        color: $c-brilliant-rose;
    }
    &--quizPage {
        position: absolute;
        top: calc((85 - 56) / 2)px;
        right: 0;
        width: 85px;
        height: 85px;
        @extend %fontSize--xl;
    }
    &--scorePage {
        z-index: 2;
        width: 140px;
        height: 140px;
        @extend %fontSize--xxl;
    }
}

@media (max-width: $break-l) {
    .counter {
        &--quizPage {
            width: 64px;
            height: 64px;
        }
        &--scorePage {
            font-size: 32px;
            width: 130px;
            height: 130px;
        }
    }
}

@media (max-width: $break-s) {
    .counter--quizPage {
        width: 50px;
        height: 50px;
        font-size: 16px;
        margin-top: 0px;
    }
    .counter--scorePage {
        width: 120px;
        height: 120px;
        font-size: 24px;
    }
}